<template>
    <div class="integration-presentation">
        <div style="background-repeat:no-repeat;transform-origin:0 0 0;"
             class="canvas-panel">
            <template v-if="config && config.layout">
                <component v-if="config.layout"
                           :is="loader[config.layout].default"
                           :state="state"
                           :config="config"></component>
            </template>
        </div>
    </div>
</template>

<script>
    import '../assets/styles/integration-presentation/main.scss'
    import API from "../config/api";

    export default {
        name: "IntegrationPresentation",
        data() {
            return {
                // 动态加载组件的存储
                loader: {},
                config: null,
                state: null
            }
        },
        created() {
            this.init()
        },
        methods: {
            init() {
                let id = this.$route.query.id
                if (!id) {
                    this.loading = false
                    this.$message.error('错误：未找到可视化。')
                    return
                }
                this.ajax({
                    url: API.INTEGRATION.GET_BY_ID,
                    params: {id: id},
                }).then(response => {
                    // let config = response.data
                    let config = {
                        "id": "b424452c-0efe-460e-9dda-8d21aca6dde8",
                        "createTime": "2020-03-23T16:00:00.000+0000",
                        "createUserId": null,
                        "updateTime": null,
                        "updateUserId": null,
                        "delFlag": "ACTIVE",
                        "name": "未命名",
                        "description": null,
                        "cover": null,
                        "layout": "SampleNingbo",
                        "navigation": "[{\"text\":\"菜单1\",\"icon\":\"\",\"image\":\"\",\"url\":\"\",\"children\":[{\"text\":\"子菜单1\",\"icon\":\"\",\"image\":\"\",\"url\":\"http://localhost:8080/#/presentation?id=36acd6be-9d69-40e6-8365-ce06b864e3b5\",\"children\":[]}]},{\"text\":\"菜单2\",\"icon\":\"\",\"image\":\"\",\"url\":\"\"}]",
                        "base": "{\"size\":{\"width\":1366,\"height\":768},\"backgroundColor\":\"rgba(0,0,0,0)\",\"backgroundImage\":null,\"scaleMode\":\"0\",\"style\":{\"backgroundColor\":\"#545c64\",\"activeColor\":null,\"item\":{\"height\":null,\"activeColor\":null},\"font\":{\"color\":\"#fff\",\"activeColor\":\"#ffd04b\",\"size\":null}}}",
                        "state": "ON"
                    }
                    if (!config) {
                        this.$message.error('错误：未找到系统。')
                    }
                    config.base = JSON.parse(config.base)
                    config.navigation = JSON.parse(config.navigation)
                    this.config = config
                }).finally(() => {
                    this.loading = false
                })
            },
            /**
             * 计算背景样式
             */
            calcBgStyle() {
                if (!this.config || !this.config.base) {
                    return
                }
                return {
                    width: (this.config.base.size.width + 60) + 'px',
                    height: (this.config.base.size.height + 60) + 'px'
                };
            },
            /**
             * 计算画布样式
             */
            calcPaintStyle() {
                if (!this.config || !this.config.base) {
                    return
                }
                return {
                    width: this.config.base.size.width + 'px',
                    height: this.config.base.size.height + 'px',
                    backgroundColor: this.config.base.backgroundColor,
                    backgroundImage: this.config.base.backgroundImage ?
                        'url(' + this.config.base.backgroundImage + ')' : 'none',
                    backgroundSize: this.calcBgImageSize(),
                }
            },
            /**
             * 根据配置项调整背景图片尺寸
             */
            calcBgImageSize() {
                if (!this.config || !this.config.base) {
                    return
                }
                switch (this.config.base.scaleMode) {
                    case "1":
                        return 'auto 100%'
                    case "2":
                        return '100% 100%'
                    default:
                        return '100% auto'
                }
            },
            /**
             * 动态加载组件
             */
            loadComponent() {
                if (!this.config) {
                    return
                }
                let layout = this.config.layout;
                this.loader[layout] = require('../components/integration/layout/' + layout + ".vue")
            },
        },
        watch: {
            config: {
                immediate: true,
                handler: function () {
                    this.loadComponent()
                }
            },
            $route: function () {
                this.init()
            }
        },
    }
</script>

