<template>
    <div class="integration-presentation">
        <el-menu :default-active="activeIndex"
                 class="el-menu-demo"
                 mode="horizontal"
                 @select="handleSelect"
                 :background-color="config.base.style.backgroundColor || defaultStyle.backgroundColor"
                 :text-color="config.base.style.font.color || defaultStyle.textColor"
                 :active-text-color="config.base.style.font.activeColor || defaultStyle.activeTextColor">
            <template v-if="config.navigation && config.navigation.length">
                <template v-for="(item, i) in config.navigation">
                    <template v-if="item.children && item.children.length">
                        <el-submenu :index="i + ''" :key="i">
                            <template slot="title">{{item.text}}</template>
                            <template v-for="(child, j) in item.children">
                                <template v-if="child.children && child.children.length">
                                    <el-submenu :index="i + '-' + j" :key="j">
                                        <template slot="title">{{child.text}}</template>
                                        <template v-for="(grandson, k) in child.children">
                                            <el-menu-item :index="i + '-' + j + '-' + k" :key="k">{{grandson.text}}
                                            </el-menu-item>
                                        </template>
                                    </el-submenu>
                                </template>
                                <template v-else>
                                    <el-menu-item :index="i + '-' + j" :key="j">{{child.text}}</el-menu-item>
                                </template>
                            </template>
                        </el-submenu>
                    </template>
                    <template v-else>
                        <el-menu-item :key="i" :index="i + ''">{{item.text}}</el-menu-item>
                    </template>
                </template>
            </template>
        </el-menu>
        <div class="integration-iframe" style="top: 61px;">
            <iframe v-if="url"
                    class="integration-iframe"
                    :src="url"
                    frameborder="0"></iframe>
        </div>
    </div>
</template>

<script>
    import _ from "lodash";

    export default {
        name: "TopNav",
        props: ["config", "state"],
        data() {
            return {
                activeIndex: '',
                defaultStyle: {
                    backgroundColor: '#545c64',
                    textColor: '#ffffff',
                    activeTextColor: '#ffd04b',
                    size: {
                        width: 1366,
                        height: 768
                    }
                },
                defaultMenu: [{
                    text: "菜单1",
                    icon: "",
                    image: "",
                    url: "",
                    children: [{
                        text: "子菜单1",
                        icon: "",
                        image: "",
                        url: "",
                        children: [{
                            text: "选项1",
                            icon: "",
                            image: "",
                            url: ""
                        }, {
                            text: "选项2",
                            icon: "",
                            image: "",
                            url: ""
                        }]
                    }]
                }, {
                    text: "菜单2",
                    icon: "",
                    image: "",
                    url: "",
                }],
                url: null
            };
        },
        mounted() {
            if (!this.config.navigation) {
                this.config.navigation = this.defaultMenu;
            }
            this.config.base.size = _.assignIn({}, this.defaultStyle.size, this.config.base.size)
            this.config.base.defaultStyle = this.defaultStyle
        },
        methods: {
            handleSelect(key, keyPath) {
                let arr = key.split("-")
                let index = parseInt(arr[0])
                let nav = this.config.navigation[index]
                if (arr.length > 1) {
                    for (let i = 1; i < arr.length; i++) {
                        index = parseInt(arr[i]);
                        nav = nav.children[index];
                    }
                }
                if (this.url === nav.url) {
                    return
                }
                this.url = nav.url;
            }
        }
    }
</script>

